import React from 'react';
import { Box, Typography, Paper, Divider } from '@mui/material';

function ProteinDetails({ protein }) {
  if (!protein) {
    return (
      <Paper elevation={3} sx={{ p: 2, height: '100%' }}>
        <Typography variant="h6" color="textSecondary">
          
        </Typography>
      </Paper>
    );
  }

  return (
    <Paper elevation={3} sx={{ p: 2, height: '100%' }}>
      <Typography variant="h5" gutterBottom>
        {protein.id}
      </Typography>
      <Divider sx={{ my: 2 }} />
      
      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2" sx={{ wordBreak: 'break-word' }}>
          {protein.sequence}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2">
          {protein.Biological_process || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2">
          {protein.Cellular_component || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2">
          {protein.Molecular_function || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1">KOG</Typography>
        <Typography variant="body2">
          {protein.KOG || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1">KEGG</Typography>
        <Typography variant="body2">
          {protein.KEGG_Pathways || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1">IPRs</Typography>
        <Typography variant="body2">
          {protein.IPRs || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2">
          {protein.Subcellular_localization || ''}
        </Typography>
      </Box>

      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle1"></Typography>
        <Typography variant="body2">
          {protein.signalP || ''}
        </Typography>
      </Box>

      {/*  */}
      <Box sx={{ 
        display: 'flex',
        flexWrap: 'wrap',
        gap: 1,
        mt: 2
      }}>
        {protein.KEGG_Pathways && (
          <Box sx={{
            bgcolor: '#e3f2fd',
            borderRadius: 1,
            px: 1,
            py: 0.5
          }}>
            <Typography variant="caption">
              KEGG: {protein.KEGG_Pathways}
            </Typography>
          </Box>
        )}
        {protein.Subcellular_localization && (
          <Box sx={{
            bgcolor: '#f3e5f5',
            borderRadius: 1,
            px: 1,
            py: 0.5
          }}>
            <Typography variant="caption">
              : {protein.Subcellular_localization}
            </Typography>
          </Box>
        )}
        {protein.signalP && (
          <Box sx={{
            bgcolor: '#fff3e0',
            borderRadius: 1,
            px: 1,
            py: 0.5
          }}>
            <Typography variant="caption">
              : {protein.signalP}
            </Typography>
          </Box>
        )}
      </Box>
    </Paper>
  );
}

export default ProteinDetails;
